@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: "Roboto", sans-serif;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

:root {
  --clr-accent: #0f2631;
  --clr-accent-light: #1b4558;

  --clr-white: #fff;
  --clr-black: #000;

  --clr-light-black: #4b4b4b;
  --clr-lightgrey: #d3d3d3;

  --padding-small: 24px;
  --padding-large: 24px 40px;
}
/* font-family: 'Roboto', sans-serif; */
/* font-family: 'Kaushan Script', cursive; */

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

* {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

ul {
  list-style: none;
}

header {
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  z-index: 999;
}

.navbar {
  padding: var(--padding-large);

  display: flex;
  align-items: center;
  justify-content: space-between;

  background: var(--clr-accent);

  position: relative;
}

.logo {
  font-size: 28px;
  color: var(--clr-white);
  font-family: "Kaushan Script", cursive;
}

.nav-links {
  position: absolute;
  right: 0;

  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-gap: 40px;
  gap: 40px;

  padding: 16px 40px;

  width: -webkit-max-content;

  width: max-content;

  background: var(--clr-white);
  border-radius: 12px 0 0 12px;
}

.nav-links a {
  transition: color 0.2s ease-in-out;
}

.nav-links a:hover,
.nav-links a:focus {
  color: grey;
}

.btn {
  padding: 12px 24px;

  background: var(--clr-accent);

  color: var(--clr-white);
  font-weight: 700;

  transition: background 0.2s ease-in-out;
}

.btn:hover,
.btn:focus {
  background: var(--clr-accent-light);
}

.navToggle {
  display: none;
}

@media only screen and (max-width: 530px) {
  .navbar {
    padding: var(--padding-small);
  }

  .nav-links {
    grid-gap: 40px;
    gap: 40px;

    padding: 16px 24px;

    justify-content: flex-start;

    width: calc(100% - 24px);

    -webkit-transform: scale(0, 1);

            transform: scale(0, 1);
    -webkit-transform-origin: right;
            transform-origin: right;
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  }

  .nav-links li {
    /* opacity: 0;
    transition: opacity 0.3s ease-in-out 0.2s; */
    opacity: 0;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
    -webkit-transform-origin: right;
            transform-origin: right;

    transition: opacity 0.2s ease-in 0.2s, -webkit-transform 0.15s ease-in;

    transition: opacity 0.2s ease-in 0.2s, transform 0.15s ease-in;

    transition: opacity 0.2s ease-in 0.2s, transform 0.15s ease-in, -webkit-transform 0.15s ease-in;
  }

  .nav-links.active {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }

  .nav-links.active li {
    opacity: 1;
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    -webkit-transform-origin: left;
            transform-origin: left;
  }

  .navToggle {
    position: absolute;
    right: 24px;
    z-index: 1000;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    grid-gap: 8px;
    gap: 8px;

    cursor: pointer;
  }

  .navToggle .bar {
    height: 4px;
    width: 36px;
    border-radius: 4px;
    background: var(--clr-white);
    transition: background 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    transition: background 0.2s ease-in-out, transform 0.2s ease-in-out;
    transition: background 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  }

  .navToggle.active .bar {
    background: var(--clr-accent);
  }

  .navToggle.active .bar-one {
    -webkit-transform: translateY(6px) rotate(45deg);
            transform: translateY(6px) rotate(45deg);
  }

  .navToggle.active .bar-two {
    -webkit-transform: translateY(-6px) rotate(-45deg);
            transform: translateY(-6px) rotate(-45deg);
  }
}

.carousel-cell {
  width: 100%;
  height: 100vh;

  position: relative;
  z-index: -1;
}

.carousel-cell::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;

  background: rgba(0, 0, 0, 0.5);
}

.carousel-cell img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.flickity-button {
  transition: background 0.2s ease-in-out;
}

.flickity-button:disabled {
  opacity: 0.35;
}

.flickity-prev-next-button.previous {
  left: 40px;
}
.flickity-prev-next-button.next {
  right: 40px;
}

.flickity-page-dots {
  bottom: 40px;
}

.flickity-page-dots .dot {
  background: var(--clr-lightgrey);
  opacity: 0.5;
  transition: background 0.2s ease-in-out;
}

.flickity-page-dots .dot.is-selected {
  background: var(--clr-white);
}

.card-container {
  min-height: 100vh;
  padding: 100px 40px;

  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 24px;
  gap: 24px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 24px;
  gap: 24px;

  padding: var(--padding-small);

  background: var(--clr-lightgrey);
  border-radius: 16px;
}

.card img {
  height: 125px;
  width: 125px;
  border-radius: 50%;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 16px;
  gap: 16px;
}

.price {
  color: var(--clr-light-black);
}

.price span {
  margin-right: 8px;

  color: var(--clr-black);
  font-size: 18px;
}

.buttons {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
}

.buttons span {
  margin-right: 8px;
}

.buttons .btn {
  padding: 12px 16px;
  background: var(--clr-accent-light);
}

.buttons .btn:hover,
.buttons .btn:focus {
  background: var(--clr-accent);
}

@media only screen and (max-width: 400px) {
  .card-container {
    padding: 100px 24px;
  }
}

@media only screen and (max-width: 365px) {
  .card-container {
    padding: 100px 16px;
  }

  .card {
    padding: 24px 16px;
  }
}

@media only screen and (min-width: 1400px) {
  .card-container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.form-container {
  min-height: 100vh;

  display: flex;
  align-items: flex-end;
  justify-content: center;

  padding: 40px;
}

* {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

form {
  background: var(--clr-lightgrey);
  border-radius: 8px;

  width: 350px;

  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;

  padding: var(--padding-small);

  box-shadow: 4px 4px 12px -4px rgba(0, 0, 0, 0.5);
}

form h2 {
  font-size: 18px;

  margin-bottom: 8px;
}

form input:not([type="radio"]) {
  border: none;
  outline: none;
  padding: 10px 16px;
  border-radius: 4px;

  width: 100%;

  color: #6e6e6e;
}

::-webkit-input-placeholder {
  color: inherit;
}

:-ms-input-placeholder {
  color: inherit;
}

::placeholder {
  color: inherit;
}

form .btn-sign-up {
  border-radius: 4px;
  margin-top: 16px;
}

form input[type="submit"] {
  color: var(--clr-white);
}

.gender-tick {
  display: flex;
  align-items: center;
  justify-content: space-between;

  color: #6e6e6e;
}

@media only screen and (max-height: 730px) {
  .form-container {
    padding: 100px 40px;
  }
}

@media only screen and (min-height: 770px) {
  .form-container {
    align-items: center;
  }
}

@media only screen and (max-width: 400px) {
  .form-container {
    padding: 100px 24px;
  }

  form {
    width: 100%;
  }
}

@media only screen and (max-width: 365px) {
  .form-container {
    padding: 100px 16px;
  }

  form {
    padding: 24px 16px;
  }
}

